<template>
	<div>
		<ds-nav :pagename="pagename"></ds-nav>
		<div class="page-wrapper">
			<div class="page-info uk-visible@s">
				<h1 class="page-name"> {{ pagename }} </h1>
				<div class="page-name-border" style="width: 100px;"></div>	
			</div>
			<div uk-grid>
				<div class="uk-width-small@m">
			        <ul class="side-list uk-nav uk-nav-default" uk-switcher="connect: #component-nav; animation: uk-animation-fade; toggle: > :not(.uk-nav-header)">
			            <li id="about-toggle" @click="setPagename(' ')"><a href="#"> </a></li>
						<li id="terms-toggle" @click="setPagename(' ')"><a href="#"> </a></li>
						<li id="disclaimer-toggle" @click="setPagename(' ')"><a href="#"> </a></li>
						<li id="policy-toggle" @click="setPagename(' ')"><a href="#"> </a></li>
			        </ul>
			    </div>
			    <div class="uk-width-expand@m">
			        <ul id="component-nav" class="uk-switcher">
			            <li id="about-section">
			            	<div>
					    		<p>                                       .</p>
								<p>                      .       <a @click.stop="showTerms()"> </a>.</p>
								<p>                          .</p>
								<p>
								          .
								</p>
								<p>     !          .              .               <a
								@click.stop="showPolicy()"> </a>       .</p>
								<p>
									                      <a @click.stop="showDisclaimer()">  </a>.
								</p>
								<p>         .</p>
								<p>    :)</p>
					    	</div>
			            </li>
			            <li id="terms-section">
			            	<div>
					    		<p>    &nbsp;           :</p>
								<ul class="bulky-list">
									<li>      &nbsp;</li>
									<li>  ""         &nbsp; <a @click.stop="showDisclaimer()">  </a> &nbsp;<a @click.stop="showPolicy()">  </a></li>
									<li>       &nbsp;        <a @click.stop="showDisclaimer()">  </a></li>
									<li>              </li>
									<li>                    </li>
									<li>   &nbsp;      &nbsp;</li>
									<li>           &nbsp;  </li>
								</ul>
					    	</div>
			            </li>
			            <li id="disclaimer-section">
			            	<div>
					    		<p>    ""   &nbsp;   &nbsp;        dawasawa.online &nbsp; &nbsp; &nbsp;               .</p>

								<p>   ""   &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;   &nbsp;                     .</p>

								<p>                                   &nbsp;   . &nbsp;       &nbsp;  .</p>
								<p>      &nbsp;  &nbsp;  &nbsp;   &nbsp;     &nbsp;        &nbsp;    &nbsp;    &nbsp;                &nbsp;        .</p>
								<p>            &nbsp;                   &nbsp;     .</p>
								<p>      &nbsp;      &nbsp;      &nbsp;         <a @click.stop="showPolicy()"> </a> &nbsp;                  &nbsp;.            .</p>
								<p>                  &nbsp;             &nbsp;              &nbsp;                &nbsp;   &nbsp;  &nbsp;                     .         :   &nbsp;   &nbsp;  .</p>
								<p>        &nbsp;   &nbsp;       &nbsp;         &nbsp;  . &nbsp;                       &nbsp;                                &nbsp;                                          .</p>
								<p>                &nbsp;        .</p>
					    	</div>
			            </li>
			            <li id="policy-section">
			            	<div>
					    		<p>            &nbsp;            &nbsp;         .</p>
								<p>                   . &nbsp;                        &nbsp;   &nbsp;       &nbsp;             .</p>
								<p>        :</p>
								<ul>
									<li>              </li>
									<li>    </li>
									<li>   </li>
								</ul>
								<div style="height: 12px;"></div>
								<p>          &nbsp;           .      :</p>
								<ul>
									<li> </li>
									<li></li>
									<li>   </li>
									<li>    </li>
									<li> &nbsp;     .</li>
								</ul>
								<p>             &nbsp;    &nbsp;       .        .</p>
								<p>      &nbsp;        &nbsp;    .</p>
								<p>            &nbsp;    .</p>
					    	</div>
			            </li>
			        </ul>
			    </div>
			</div>
		</div>
		
	</div>
</template>

<script>
	import Navbar from '../components/nav_bar.vue';
	import Alert from '../components/alert.vue';

	const navigation = require('../data/navigation.json');

	export default {
		data() {
			return {
				pagename: " "
			}
		},
		components: {
			'ds-nav': Navbar,
			'ds-alert': Alert
		},
		methods: {
			setPagename(name) {
				this.pagename = name
			},
			showPolicy() {
				// remove visibility classes from all elements
				$('.side-list li').attr("aria-expanded", function() {
					return false
				}).removeClass('uk-active')
				$('#component-nav li').removeClass('uk-active')
				// add visibility classes to the required elements
				$('#policy-toggle').attr("aria-expanded", function() {
					return true
				}).addClass('uk-active')
				$('#policy-section').addClass('uk-active')
			},
			showTerms() {
				// remove visibility classes from all elements
				$('.side-list li').attr("aria-expanded", function() {
					return false
				}).removeClass('uk-active')
				$('#component-nav li').removeClass('uk-active')
				// add visibility classes to the required elements
				$('#terms-toggle').attr("aria-expanded", function() {
					return true
				}).addClass('uk-active')
				$('#terms-section').addClass('uk-active')
			},
			showDisclaimer() {
				// remove visibility classes from all elements
				$('.side-list li').attr("aria-expanded", function() {
					return false
				}).removeClass('uk-active')
				$('#component-nav li').removeClass('uk-active')
				// add visibility classes to the required elements
				$('#disclaimer-toggle').attr("aria-expanded", function() {
					return true
				}).addClass('uk-active')
				$('#disclaimer-section').addClass('uk-active')
			}
		}
	}
</script>

<style>
	.uk-nav-default>li.uk-active>a {
		color: #e84c3d;
	}
	#component-nav p {
		font-size: 1.2rem;
		line-height: 2;
		word-spacing: 0.2rem;
		text-align: justify;
		max-width: 1200px;
	}
	@media screen and (max-width: 640px) {
		#component-nav p {
			font-size: 1rem;
			line-height: 1.8;
			word-spacing: 0.2rem;
		}
	}
</style>


